.Header_rotateShadows__2N4wl {
  width: 27px;
  height: 27px;
  position: relative;
}
.Header_rotateShadows__2N4wl:after,
.Header_rotateShadows__2N4wl:before {
  content: "";
  border-radius: 2%;
  position: absolute;
  padding: 8px;
  margin: -5px 25px;
  width: 100%;
  height: 100%;
  transform-origin: center center;
}
.Header_rotateShadows__2N4wl:before {
  box-shadow: inset 0 20px 0 rgba(0, 250, 250, 0.6),
    inset 20px 0 0 rgba(0, 200, 200, 0.6),
    inset 0 -20px 0 rgba(0, 150, 200, 0.6),
    inset -20px 0 0 rgba(0, 200, 250, 0.6);
  animation: Header_rotate-before__-jP7U 2s -0.5s linear infinite;
}
.Header_rotateShadows__2N4wl:after {
  box-shadow: inset 0 20px 0 rgba(250, 250, 0, 0.6),
    inset 20px 0 0 rgba(250, 200, 0, 0.6),
    inset 0 -20px 0 rgba(250, 150, 0, 0.6),
    inset -20px 0 0 rgba(250, 100, 0, 0.6);
  animation: Header_rotate-after__2ZDHG 2s -0.5s linear infinite;
}
@keyframes Header_rotate-after__2ZDHG {
  0% {
    transform: rotateZ(0deg) scaleX(1) scaleY(1);
  }
  50% {
    transform: rotateZ(180deg) scaleX(0.82) scaleY(0.95);
  }
  100% {
    transform: rotateZ(360deg) scaleX(1) scaleY(1);
  }
}
@keyframes Header_rotate-before__-jP7U {
  0% {
    transform: rotateZ(0deg) scaleX(1) scaleY(1);
  }
  50% {
    transform: rotateZ(-180deg) scaleX(0.95) scaleY(0.85);
  }
  100% {
    transform: rotateZ(-360deg) scaleX(1) scaleY(1);
  }
}

